@import url(https://fonts.googleapis.com/css?family=Belleza&display=swap);
@import url(https://fonts.googleapis.com/css?family=Bellefair&display=swap);
@import url(https://fonts.googleapis.com/css?family=Ubuntu&display=swap);
@import url(https://fonts.googleapis.com/css?family=Anton&display=swap);
@import url(https://fonts.googleapis.com/css?family=Ubuntu&display=swap);
@import url(https://fonts.googleapis.com/css?family=Ubuntu&display=swap);
/* GLOBAL */
html, body {
  margin: 0;
  padding: 0; }

*, *::after, *::before {
  box-sizing: border-box; }

body::-webkit-scrollbar {
  width: 25px; }

body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px #939393;
  border-radius: 20px; }

body::-webkit-scrollbar-thumb {
  background: rgba(147, 147, 147, 0.5);
  border-radius: 10px; }

body::-webkit-scrollbar-thumb:hover {
  background: rgba(147, 147, 147, 0.8); }

body {
  background: #0D0C1D;
  color: #96a8a2;
  font-family: 'Belleza', sans-serif;
  height: 100vh;
  text-rendering: optimizeLegibility; }

input {
  color: #96a8a2;
  width: 2em;
  height: 2em; }

.home {
  margin-top: 3em; }

nav {
  box-shadow: 0px 1px 100px 4px rgba(167, 148, 238, 0.8);
  z-index: 10; }

.nav-wrapper {
  background-color: rgba(125, 91, 190, 0.8); }

.responsive {
  font-family: 'Anton', sans-serif; }

.brand-logo {
  font-family: 'Bellefair', serif; }

.hamburger {
  position: absolute;
  top: 2%;
  right: 2rem;
  display: none;
  flex-direction: column;
  justify-content: space-around;
  width: 2rem;
  height: 2rem;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  z-index: 10;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease; }

.hamburger span {
  border: white 2px solid;
  border-radius: 20px; }

.results {
  height: 300px;
  display: grid;
  grid-gap: 50px;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  align-items: center;
  justify-items: center;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease; }

.post {
  background: #0D0C1D;
  border: rgba(60, 119, 236, 0) outset 1.5px;
  border-radius: 0.5em;
  background-clip: padding-box; }

.post:hover {
  border: #3c77ec inset 1.5px;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease; }

.card-content {
  text-align: center;
  color: #96a8a2;
  height: 320px; }

.card-content span {
  color: #606095;
  font-family: 'Ubuntu', sans-serif; }

.no-results {
  position: absolute;
  left: 50%;
  -webkit-transform: translate(-50%, 1000%);
          transform: translate(-50%, 1000%); }

@-webkit-keyframes navAppear {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes navAppear {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@media screen and (max-width: 768px) {
  .hamburger {
    display: flex; }
  .right {
    display: none; }
  .right li {
    opacity: 0; }
  .responsive {
    -webkit-transition: 0.3s ease all;
    transition: 0.3s ease all;
    padding: 1em 0px;
    width: 100vw;
    left: 0;
    position: absolute;
    display: flex;
    flex-direction: column;
    background: rgba(0, 0, 0, 0.8);
    justify-content: center;
    align-items: center;
    z-index: 10;
    top: 8.85%;
    -webkit-animation: navAppear 0.3s ease-out forwards;
            animation: navAppear 0.3s ease-out forwards; }
  .responsive li {
    border-bottom: 1px solid transparent;
    -webkit-transition: 0.3s ease all;
    transition: 0.3s ease all;
    opacity: 1; }
  .responsive li:hover {
    border-bottom: white 1px solid; } }

.result-container {
  display: grid;
  align-items: center;
  justify-items: center;
  grid-template-columns: 1fr 4fr;
  grid-template-rows: auto;
  grid-template-areas: "head match" "body match"; }

.profile-head {
  display: grid;
  align-items: center;
  justify-items: center;
  justify-items: stretch;
  grid-area: head;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 2em; }

.profile-head h1 {
  font-family: 'Ubuntu', sans-serif;
  -webkit-text-stroke: rgba(147, 147, 147, 0.5);
  -webkit-text-fill-color: #2b2b9f;
  -webkit-text-stroke-width: 0.5px; }

.profile-head img:first-child {
  border-radius: 10px;
  border: #858585 solid 2px; }

.profile-body {
  display: grid;
  align-items: center;
  justify-items: center;
  grid-template-columns: 1fr 1fr;
  grid-area: body; }

.match-container {
  grid-area: 1 / col2-start / last-line / 2; }

.match-container input {
  border-top-left-radius: 20px !important;
  border-bottom-left-radius: 20px !important;
  padding-left: 10px !important; }

.match-container input::-webkit-input-placeholder {
  opacity: 0.3; }

.match-container input::-moz-placeholder {
  opacity: 0.3; }

.match-container input:-ms-input-placeholder {
  opacity: 0.3; }

.match-container input::-ms-input-placeholder {
  opacity: 0.3; }

.match-container input::placeholder {
  opacity: 0.3; }

.match-container input[type=text]:valid {
  border: 3px solid rgba(125, 91, 190, 0.3);
  box-shadow: 0 1px 0 0 rgba(255, 174, 174, 0.3); }

.match-container input:focus {
  border: 3px solid rgba(125, 91, 190, 0.7) !important;
  box-shadow: 0 1px 0 0 rgba(255, 174, 174, 0.7) !important; }

.match-list {
  width: 30vw;
  height: 60vh;
  overflow: scroll;
  overflow-x: hidden; }

.match-post {
  height: 5h;
  display: grid;
  align-items: center;
  justify-items: center;
  border: 1px rgba(255, 255, 255, 0) solid;
  grid-template-columns: 1fr 1fr 1fr; }

div ::-webkit-scrollbar {
  width: 20px; }

div ::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px #7e5bbe;
  background: rgba(89, 0, 255, 0);
  border-radius: 3px; }

div ::-webkit-scrollbar-thumb {
  background: rgba(81, 30, 176, 0.5);
  border-radius: 3px; }

div ::-webkit-scrollbar-thumb:hover {
  background: #511eb0; }

.match-post:hover {
  border: 1px cyan solid; }

.match-post h4 {
  color: #8844c8;
  font-size: 25px; }

.match-body h5,
.match-body h4 {
  color: rgba(254, 254, 255, 0.5); }

.champ-icon img {
  border: black 1px solid;
  border-radius: 100px; }

.champ-icon h6 {
  text-align: center;
  color: white; }

.error {
  -webkit-text-fill-color: white;
  -webkit-text-stroke-color: #ff9292;
  -webkit-text-stroke-width: 0.3px;
  height: 40vh;
  font-family: italic;
  text-align: center;
  align-items: center; }

.erorr h5 {
  translate: transformY(30%); }

@media screen and (max-width: 740px) {
  .result-container {
    display: flex;
    flex-direction: column;
    text-align: center; }
  .profile-body {
    display: grid;
    align-items: center;
    justify-items: center;
    grid-template-columns: 1fr; }
  .match-container {
    margin: 5em; }
  .match-container,
  .match-list {
    width: 75vw; } }

@media screen and (max-width: 1000px) {
  .match-post h4 {
    font-size: 20px; }
  .champ-icon h6 {
    display: none; } }

.main-display {
  display: grid;
  align-items: center;
  justify-items: center;
  grid-template-columns: 1fr 2fr; }

.main-image {
  justify-self: end;
  display: flex;
  flex-direction: column; }

.main-image h2 {
  padding: 0px;
  margin-bottom: 0px;
  text-align: center;
  color: #606095;
  font-family: 'Ubuntu', sans-serif; }

.change-skin {
  align-self: center; }

.change-skin button {
  background: rgba(73, 65, 85, 0.5);
  border: whitesmoke 1px solid;
  color: whitesmoke; }

.change-skin button:hover {
  background: #494155; }

.spell-info {
  display: grid;
  align-items: center;
  justify-items: center;
  grid-template-columns: 1fr 1fr; }

.spell-text {
  justify-self: start; }

.spell-text h5 {
  color: blueviolet;
  -webkit-text-stroke-color: white;
  -webkit-text-stroke-width: 0.2px; }

.cd-text,
.cost-text {
  display: grid;
  align-items: center;
  justify-items: center;
  display: flex;
  justify-items: start; }

.cd-text span {
  color: #87e28d;
  -webkit-text-stroke-color: white;
  -webkit-text-stroke-width: 0.05px; }

.cost-text span {
  color: #e6e98f;
  -webkit-text-stroke-color: white;
  -webkit-text-stroke-width: 0.05px; }

.skin-screen {
  grid-template-columns: repeat(5, 1fr);
  height: 90vh;
  width: 100vw;
  position: absolute;
  background: rgba(0, 0, 0, 0.8);
  display: none;
  align-items: center;
  justify-content: center; }

.skin-screen img {
  position: relative;
  -webkit-transform: translateX(50%);
          transform: translateX(50%);
  width: 10vw;
  border: transparent 2px solid; }

.skin-screen img:hover {
  border: white 2px solid; }

.skin-select {
  grid-template-columns: repeat(6, 1fr);
  display: grid;
  align-items: center;
  position: relative; }

.open {
  display: grid; }

@media screen and (max-width: 740px) {
  .main-image img {
    max-width: 120%; }
  .main-image h2 {
    font-size: 7.5vw; }
  .spell-info img {
    max-width: 40%; }
  .spell-text h5 {
    font-size: 4vw; }
  .spell-text p,
  .spell-text span {
    font-size: 2vw; }
  .cd-text,
  .cost-text {
    display: grid;
    align-items: center;
    justify-items: center;
    justify-items: start; }
  .skin-screen img {
    width: 15vw; }
  .skin-select {
    grid-template-columns: repeat(4, 1fr); } }

